import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

// Types
export interface CallHistoryItem {
  id: string;
  call_id: string;
  customer_number: string;
  status: "queued" | "ringing" | "in-progress" | "forwarding" | "ended";
  type: "outbound" | "inbound" | "web";
  duration: number | string | null | undefined;
  cost: number | string | null | undefined;
  transcript: string;
  recording_url?: string;
  recordingUrl?: string;
  recording?: string;
  assistant_id?: string;
  assistant_name?: string;
  end_reason?: string;
  started_at?: string;
  ended_at?: string;
  created_at: string;
  updated_at: string;
  metadata?: any;
}

export interface CallHistoryResponse {
  calls: CallHistoryItem[];
  totalCalls: number;
  currentPage: number;
  totalPages: number;
  hasNextPage: boolean;
  hasPrevPage: boolean;
}

// Get call history for a specific contact by phone number
export async function getContactCallHistory(
  phoneNumber: string,
  page: number = 1,
  limit: number = 10
): Promise<CallHistoryResponse> {
  try {
    // Encode phone number to handle special characters like +
    const encodedPhoneNumber = encodeURIComponent(phoneNumber);
    
    const response = await axiosInstance.get(
      `/api/outboundcall/contact/${encodedPhoneNumber}/history`,
      {
        params: { page, limit }
      }
    );

    if (response.data.success) {
      return response.data.data;
    } else {
      throw new Error(response.data.message || "Failed to fetch call history");
    }
  } catch (error: any) {
    console.error("Error fetching call history:", error);
    const message = error.response?.data?.message || "Failed to fetch call history";
    toast.error(message);
    throw error;
  }
}

// Get call recording URL
export async function getCallRecording(callId: string) {
  try {
    const response = await axiosInstance.get(`/api/outboundcall/recording/${callId}`);

    if (response.data.success) {
      return response.data.data;
    } else {
      throw new Error(response.data.message || "Failed to get recording");
    }
  } catch (error: any) {
    console.error("Error fetching call recording:", error);

    // Don't show toast for 404 errors (recording not available)
    if (error.response?.status !== 404) {
      const message = error.response?.data?.message || "Failed to get recording";
      toast.error(message);
    }

    throw error;
  }
}

