// Format duration from seconds to readable format
export function formatDuration(seconds: number | string | null | undefined): string {
  // Handle null, undefined, or empty values
  if (seconds === null || seconds === undefined || seconds === '') {
    return "0:00";
  }

  // Convert to number if it's a string
  const numericSeconds = typeof seconds === 'string' ? parseInt(seconds, 10) : seconds;

  // Handle NaN or invalid numbers
  if (isNaN(numericSeconds) || numericSeconds === 0) {
    return "0:00";
  }

  const minutes = Math.floor(numericSeconds / 60);
  const remainingSeconds = numericSeconds % 60;

  return `${minutes}:${remainingSeconds.toString().padStart(2, '0')}`;
}

// Format call status for display
export function formatCallStatus(status: string): { text: string; color: string } {
  switch (status) {
    case "ended":
      return { text: "Completed", color: "text-green-600" };
    case "in-progress":
      return { text: "In Progress", color: "text-blue-600" };
    case "ringing":
      return { text: "Ringing", color: "text-yellow-600" };
    case "queued":
      return { text: "Queued", color: "text-gray-600" };
    case "forwarding":
      return { text: "Forwarding", color: "text-purple-600" };
    default:
      return { text: status, color: "text-gray-600" };
  }
}

// Format call cost for display
export function formatCallCost(cost: number | string | null | undefined): string {
  // Handle null, undefined, or empty values
  if (cost === null || cost === undefined || cost === '') {
    return "$0.00";
  }

  // Convert to number if it's a string
  const numericCost = typeof cost === 'string' ? parseFloat(cost) : cost;

  // Handle NaN or invalid numbers
  if (isNaN(numericCost) || numericCost === 0) {
    return "$0.00";
  }

  return `$${numericCost.toFixed(2)}`;
}

// Check if call has recording available
export function hasRecording(call: CallHistoryItem): boolean {
  // Check if we have a valid recording URL (not a mock URL)
  const recordingUrl = call.recording_url || call.recordingUrl;
  const hasMockUrl = recordingUrl && recordingUrl.includes('/api/recordings/') && recordingUrl.includes('/stream');

  return !!(
    (recordingUrl && !hasMockUrl) || // Has real recording URL
    call.recording === "available" || // Explicitly marked as available
    call.status === "ended" // Ended calls might have recordings available from VAPI
  );
}

// Get recording URL from call data
export function getRecordingUrl(call: CallHistoryItem): string | null {
  return call.recording_url || call.recordingUrl || null;
}

// Format date for display
export function formatCallDate(dateString: string): string {
  try {
    const date = new Date(dateString);
    return date.toLocaleDateString() + " " + date.toLocaleTimeString([], {
      hour: '2-digit',
      minute: '2-digit'
    });
  } catch (error) {
    return dateString;
  }
}

